import Vue from 'vue'
import VueRouter from 'vue-router'
import ThemeDefault from '../views/ThemeDefault.vue'
import ThemeLogo from '../views/ThemeLogo.vue'
import ThemeClassic from '../views/ThemeClassic.vue'
import ThemeCapsule from '../views/ThemeCapsule.vue'
import Theme3D from '../views/Theme3D.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ThemeDefault',
    component: ThemeDefault,
  },
  {
    path: '/theme-logo',
    name: 'ThemeLogo',
    component: ThemeLogo,
  },
  {
    path: '/theme-classic',
    name: 'ThemeClassic',
    component: ThemeClassic,
  },
  {
    path: '/theme-capsule',
    name: 'ThemeCapsule',
    component: ThemeCapsule,
  },
  {
    path: '/theme-3d',
    name: 'Theme3D',
    component: Theme3D,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
