<template lang="pug">
.theme.theme-default
  .l-header
    h2 DEFAULT
    importSassFile(:path='path')
    //- showroom
    .showroom.row
      .col-4
        card(
          :configData='configData'
          :getThemeColor="getThemeColor"
          :c_themesColor="c_themesColor"
          :c_blockGroupStyle="c_blockGroupStyle"
          :c_blockStyle="c_blockStyle"
          :c_btnStyle="c_btnStyle"
          :c_iconStyle="c_iconStyle"
        )
      .col-8
        list(
          :configData='configData'
          :getThemeColor="getThemeColor"
          :c_themesColor="c_themesColor"
          :c_blockGroupStyle="c_blockGroupStyle"
          :c_blockStyle="c_blockStyle"
          :c_btnStyle="c_btnStyle"
          :c_iconStyle="c_iconStyle"
        )
    //- config
    .l-config
      .row
        //- Common Class Use:
        .group.d-flex.flex-column
          .group__options.d-flex.align-items-center
            label.label
              span.label__title &#x984F;&#x8272;
              select#commonColorClass(name='commonColorClass' v-model='configData.commonColorClass' @change="commonColorClassHandler" disabled)
                option(disabled='' value='') Please select one
                option(v-for="opt in ['default', 'black', 'red', 'green', 'blue', 'yellow']" :key='opt') {{`theme-${opt}`}}
            label.label
              b-form-group.mb-0
                b-form-checkbox(v-model='configData.globalSass.colorSwitch' name='globalSassColorSwitch' button-variant="success" switch='') 輸出色彩樣式
                b-icon(icon="question-circle-fill" v-b-tooltip.hover title="輸出色彩主題樣式到 css 檔")
            label.label
              span.label__title 尺寸
              select#commonClass(name='commonClass' v-model='configData.commonSizeClass')
                option(disabled='' value='') Please select one
                option(v-for="opt in ['small', 'normal', 'bigger']") {{`theme-${opt}`}}
            label.label
              span.label__title 導角
              select#globalSassRadius.small(name='globalSassRadius' v-model='configData.globalSass.radius')
                option(disabled='' value='') Please select one
                option(v-for='(i, index) in 10' :key='i') {{ `${index}px` }}
            
      //- Global Sass Varible:
      .row(data-title="色彩預覽" v-if='colorSwitch')
        .group.d-flex.flex-column
          //- .group__title 顏色設定:
          .group__options.d-flex.align-items-center
            span.label.colorGroup
              span.label__title 顏色
              input(type="color" name="globalSassColorBlack" v-model='configData.globalSass.colorBlack' :disabled="!colorSwitch")
              span.link(@click='configData.commonColorClass = "theme-black"' :class="{disabled: !colorSwitch}") Black
              input(type="color" name="globalSassColorRed" v-model='configData.globalSass.colorRed' :disabled="!colorSwitch")
              span.link(@click='configData.commonColorClass = "theme-red"' :class="{disabled: !colorSwitch}") Red
              input(type="color" name="globalSassColorBlue" v-model='configData.globalSass.colorBlue' :disabled="!colorSwitch")
              span.link(@click='configData.commonColorClass = "theme-blue"' :class="{disabled: !colorSwitch}") Blue
              input(type="color" name="globalSassColorGreen" v-model='configData.globalSass.colorGreen' :disabled="!colorSwitch")
              span.link(@click='configData.commonColorClass = "theme-green"' :class="{disabled: !colorSwitch}") Green
              input(type="color" name="globalSassColorYellow" v-model='configData.globalSass.colorYellow' :disabled="!colorSwitch")
              span.link(@click='configData.commonColorClass = "theme-yellow"' :class="{disabled: !colorSwitch}") Yellow
      template
        .row(data-title="自訂樣式")
          //- 容器
          .group.d-flex.flex-column
            .group__title 數字容器區塊:
            .group__options.d-flex.align-items-center
              label.label
                span.label__title 邊框寬度
                select.small(name='blockGroupStyleBorderWidth' v-model='configData.blockGroupStyle.borderWidth')
                  option(disabled='' value='') Please select one
                  option(v-for='(i, index) in 5' :key='i') {{ `${index}px` }}
              label.label
                span.label__title 邊框導角
                select.small(name='blockGroupStyleBorderRadius' v-model='configData.blockGroupStyle.borderRadius')
                  option(disabled='' value='') Please select one
                  option(v-for='(i, index) in 10' :key='i') {{ `${index}px` }}
            .group__options.d-flex.align-items-center
              label.label
                span.label__title 邊框顏色
                input(type="color" name="blockGroupStyleBorderColor" id="blockGroupStyleBorderColor" v-model='configData.blockGroupStyle.borderColor' @change="blockGroupStyleBorderColorHandler" :disabled="colorSwitch")
              label.label
                span.label__title 背景顏色
                input(type="color" name="blockGroupStyleBackgrouncColor" id="blockGroupStyleBackgrouncColor" v-model='configData.blockGroupStyle.backgroundColor' :disabled="colorSwitch")
              label.label
                span.label__title 文字顏色
                input(type="color" name="blockGroupStyleColor" id="blockGroupStyleColor" v-model='configData.blockGroupStyle.color' :disabled="colorSwitch")
          //- 數字
          .group.d-flex.flex-column
            .group__title 數字:
            .group__options.d-flex.align-items-center
              label.label
                span.label__title 文字顏色
                input(type="color" name="blockGroupStyleColor" id="blockGroupStyleColor" v-model='configData.blockStyle.color' :disabled="colorSwitch")
          //- 按鈕
          .group.d-flex.flex-column
            .group__title 加減按鈕:
            .group__options.d-flex.align-items-center
              label.label
                span.label__title 邊框寬度
                select.small(name='btnStyleBorderWidth' v-model='configData.btnStyle.borderWidth')
                  option(disabled='' value='') Please select one
                  option(v-for='(i, index) in 5' :key='i') {{ `${index}px` }}
              label.label
                span.label__title 邊框導角
                select.small(name='btnStyleBorderRadius' v-model='configData.btnStyle.borderRadius')
                  option(disabled='' value='') Please select one
                  option(v-for='(i, index) in 10' :key='i') {{ `${index}px` }}
            .group__options.d-flex.align-items-center
              label.label
                span.label__title 邊框顏色
                input(type="color" name="btnStyleBorderColor" v-model='configData.btnStyle.borderColor' :disabled="colorSwitch")
              label.label
                span.label__title 背景顏色
                input(type="color" name="btnStyleBackgrouncColor" v-model='configData.btnStyle.backgroundColor' :disabled="colorSwitch")
              label.label
                span.label__title 圖標顏色
                input(type="color" name="btnStyleIconFill" v-model='configData.iconStyle.stroke' :disabled="colorSwitch")
  hr
  downloadSassFile(@doCopy='doCopy')
</template>

<script>
import card from '@/components/card.vue'
import list from '@/components/list.vue'
import configDataMixin from './configData.mixin.js'
import themeMixin from './theme.mixin.js'
import copyMixin from '@/mixins/copy.mixin.js'
export default {
  mixins: [configDataMixin, themeMixin, copyMixin],
  components: { card, list },
  data() {
    return {
      path: './components/style/themes/default.sass',
    }
  },
  computed: {
    c_themesColor() {
      if (this.configData.globalSass.colorSwitch !== true)
        return { blockGroup: {}, block: {} }
      return {
        blockGroup: {
          borderColor: this.getThemeColor,
        },
        block: {
          color: '#555555',
        },
      }
    },
    c_blockGroupStyle() {
      return {
        borderRadius: `${
          this.configData.blockGroupStyle.borderRadius
            ? this.configData.blockGroupStyle.borderRadius
            : this.configData.globalSass.radius
        }`,
        borderWidth: `${this.configData.blockGroupStyle.borderWidth}`,
        borderColor: `${this.configData.blockGroupStyle.borderColor}`,
        backgroundColor: `${this.configData.blockGroupStyle.backgroundColor}`,
        color: `${this.configData.blockGroupStyle.color}`,
      }
    },
    c_blockStyle() {
      return {
        borderRadius: `${
          this.configData.blockStyle.borderRadius
            ? this.configData.blockStyle.borderRadius
            : this.configData.globalSass.radius
        }`,
        borderWidth: `${this.configData.blockStyle.borderWidth}`,
        borderColor: `${this.configData.blockStyle.borderColor}`,
        backgroundColor: `${this.configData.blockStyle.backgroundColor}`,
        color: `${this.configData.blockStyle.color}`,
      }
    },
    c_btnStyle() {
      return {
        borderRadius: `${
          this.configData.btnStyle.borderRadius
            ? this.configData.btnStyle.borderRadius
            : this.configData.globalSass.radius
        }`,
        borderWidth: `${this.configData.btnStyle.borderWidth}`,
        borderColor: `${this.configData.btnStyle.borderColor}`,
        backgroundColor: `${this.configData.btnStyle.backgroundColor}`,
      }
    },
  },
}
</script>

<style lang="sass" src="./ThemeDefault.sass" scope></style>
