import showMixin from './show_mixin.js'
export default {
  name: 'numberSpinner',
  mixins: [showMixin],
  data() {
    return {
      isCurrent: true,
      isAnimation: false, // 動效滾動中
      /**
       * 加減事件樣式名稱
       * @type {String}
       * @value plus | minus
       * @private
       */
      animationType: 'plus', // dir type class
      icon: {
        minus: 'dash',
        plus: 'plus',
      },
    }
  },
  props: {
    /**
     * 支援 bootstrap-vue icon
     * 使用方法 :
     * bootstrap
     * :bootstrap="{ minus: 'caret-left-fill', plus: 'caret-right-fill' }"
     */
    bootstrap: {
      type: Boolean | Object,
      default: false,
    },
    /**
     * 啓用動態效果
     * @values true | false
     */
    transition: {
      type: Boolean,
      default: true,
    },
    /** 最小值 */
    min: {
      type: Number,
      default: 0,
    },
    /** 最大值 */
    max: {
      type: Number,
      default: 10,
    },
    /** 間隔 */
    step: {
      type: Number | String,
      default: 1,
      validator(value) {
        return Number(value) === value && value % 1 === 0
      },
    },
    /** 值 */
    value: {
      type: Number | String,
      default: 0,
    },
  },
  watch: {
    value: {
      handler(newValue) {
        if (newValue == '') {
          this.$emit('update:value', 0)
          return
        }
        if (newValue > this.max) {
          this.$emit('update:value', this.max)
        } else if (newValue < this.min) {
          this.$emit('update:value', this.min)
        }
      },
      immediate: true,
    },
  },
  filters: {
    toNumber(value) {
      return value * 1
    },
  },
  methods: {
    /** @prive */
    com(type) {
      if (this.isAnimation) return
      this.isAnimation = true
      this.animationType = type
      let validatorFail =
        type == 'plus' ? this.value >= this.max : this.value <= this.min
      if (validatorFail) {
        this.isAnimation = false
        return
      }
      let dir = type == 'plus' ? 1 : -1
      /**
       * 更新事件
       * @property {Number} value
       */
      this.$emit('update:value', this.value * 1 + this.step * dir)
      this.isCurrent = !this.isCurrent
      setTimeout(() => {
        this.isAnimation = false
      }, 100)
    },
    /** @prive */
    plus() {
      this.com('plus')
    },
    /** @prive */
    minus() {
      this.com('minus')
    },
  },
  computed: {
    hasBootstrap() {
      return this.bootstrap !== false
    },
    getIcon() {
      let res = { ...this.icon }
      if (this.hasBootstrap) {
        res = { ...res, ...this.bootstrap }
      }
      return res
    },
    klass() {
      return {
        numberSpinner: {
          'numberSpinner--plus': this.animationType == 'plus',
          'numberSpinner--minus': this.animationType == 'minus',
        },
        btnMinus: {
          disabled: this.value <= this.min,
        },
        btnPlus: {
          disabled: this.value >= this.max,
        },
      }
    },
  },
}
