/**
 * 實作原型
 */

export default {
  watch: {
    colorSwitch(newValue) {
      this.reset()
      if (!newValue) {
        this.configData.commonColorClass = 'theme-default'
      }
    },
  },
  computed: {
    colorSwitch() {
      return this.configData.globalSass.colorSwitch
    },
    getThemeColor() {
      let res = ''
      switch (this.configData.commonColorClass) {
        case 'theme-black':
          res = this.configData.globalSass.colorBlack
          break
        case 'theme-red':
          res = this.configData.globalSass.colorRed
          break
        case 'theme-blue':
          res = this.configData.globalSass.colorBlue
          break
        case 'theme-green':
          res = this.configData.globalSass.colorGreen
          break
        case 'theme-yellow':
          res = this.configData.globalSass.colorYellow
          break
        default:
          res = ''
          break
      }
      return res
    },
    // 行內樣式輸出(style) ex: { border: '3px' }
    // 於各主題實作
    /** 主題樣式用 */
    c_themesColor() {
      return {
        blockGroup: {},
        block: {},
      }
    },
    c_blockGroupStyle() {
      return {}
    },
    c_blockStyle() {
      return {}
    },
    c_btnStyle() {
      return {}
    },
    c_iconStyle() {
      return {
        stroke: `${this.configData.iconStyle.stroke}`,
      }
    },
  },
  methods: {
    reset() {
      this.configData = Object.assign({}, this.initData)
      // Object.keys(this.initData).map(lv1Key => {
      //   if (typeof this.initData[lv1Key] == 'object') {
      //     Object.keys(this.initData[lv1Key]).map(lv2key => {
      //       this.configData[lv1Key][lv2key] = this.initData[lv1Key][lv2key]
      //     })
      //   } else {
      //     this.configData[lv1Key] = this.initData[lv1Key]
      //   }
      // })
    },
    commonColorClassHandler() {
      if (this.configData.globalSass.colorSwitch !== true) return
      if (this.configData.commonColorClass == '') return
      this.configData.blockGroupStyle.borderColor = ''
    },
    blockGroupStyleBorderColorHandler() {
      if (this.configData.globalSass.colorSwitch) return
      this.configData.commonColorClass = 'theme-default'
    },
    blockStyleBorderColorHandler() {
      if (this.configData.globalSass.colorSwitch) return
      this.configData.commonColorClass = 'theme-default'
    },
  },
  created() {
    if (this.initData == null) {
      this.initData = this.configData
    }
  },
}
